#img-wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 100px 10px 80px;
}

.img-title-wrapper {
  position: relative;
  font-weight: bolder;
  font-size: 30px;
}

.movie-img {
  width: 220px;
  max-height: 327px;
  min-height: 326px;
  margin: 10px 20px;
  cursor: pointer;
  box-shadow: 0px 0px 10px  #ccc;
  border-radius: 6px;
  
}

.hover-title {
  display: none;
  width: 220px;
  position: absolute;
  left: 50%;
  bottom: 19px;
  transform: translateX(-50%);
  padding: 13px;
  color: white;
  background-color: rgba(0, 0, 0, 0.55);
  cursor: pointer;
  box-sizing: border-box;

  border-radius: 6px;
}


.img-title-wrapper:hover  .hover-title {
    display: block;
}

.img-title-wrapper:hover {
  transform: scale(1.02);
  transition: all 0.2s;
}

h1 {
  position: absolute;
  top: 20px;
  left: 140px;
  font-size: 40px;
  color: rgb(34, 17, 17);
}